import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';

// --- Helper Functions ---
const getPriceValue = (priceString) => {
    if (!priceString) return 0;
    const price = parseFloat(String(priceString).replace(',', '.').replace(' zł', ''));
    return isNaN(price) ? 0 : price;
};

const getLowestPrice = (product) => {
    if (!product || !product.heightOptions || product.heightOptions.length === 0) return 0;
    const prices = product.heightOptions.map(opt => getPriceValue(opt.promoPrice || opt.price));
    return Math.min(...prices);
};

const hasPromotion = (product) => {
    if (!product || !product.heightOptions) return false;
    return product.heightOptions.some(opt => opt.promoPrice);
};

const HomePage = () => {
    const [products, setProducts] = useState([]);
    const [productsError, setProductsError] = useState(null);
    const [configData, setConfigData] = useState(null);
    const [configError, setConfigError] = useState(null);

    // Fetch products data
    useEffect(() => {
        const fetchProducts = async () => {
            try {
                const response = await fetch('/products.json');
                if (!response.ok) {
                    throw new Error(`HTTP error! status: ${response.status}`);
                }
                const data = await response.json();
                setProducts(data);
            } catch (error) {
                console.error('Error fetching products:', error);
                setProductsError(error);
            }
        };
        fetchProducts();
    }, []);

    // Fetch config data
    useEffect(() => {
        const fetchConfig = async () => {
            try {
                const response = await fetch('/config.json');
                if (!response.ok) {
                    throw new Error(`HTTP error! status: ${response.status}`);
                }
                const data = await response.json();
                setConfigData(data);
            } catch (error) {
                console.error('Error fetching config:', error);
                setConfigError(error);
            }
        };
        fetchConfig();
    }, []);

    const featuredProducts = products.filter(p => p.isBestseller).slice(0, 4);

    if (productsError) {
        return <div className="container mt-5 pt-5 text-center alert alert-danger">Błąd ładowania produktów: {productsError.message}</div>;
    }

    if (products.length === 0) {
        return <div className="container mt-5 pt-5 text-center">Ładowanie produktów...</div>;
    }

    return (
        <>
            {/* Hero Section */}
            <section className="py-5 text-center container">
                <div className="row py-lg-5">
                    <div className="col-lg-8 col-md-10 mx-auto">
                        <h1 className="fw-light display-4">Tradycja i Nowoczesność w Druku 3D</h1>
                        <p className="lead text-muted">Odkryj unikalne produkty stworzone z pasją w Polsce. Łączymy rzemiosło z innowacyjną technologią, dostarczając wyjątkową jakość i design.</p>
                        <p>
                            <Link to="/products" className="btn btn-primary my-2">Zobacz wszystkie produkty</Link>
                            <Link to="/zamowienia" className="btn btn-outline-dark my-2 ms-2">Zamówienie niestandardowe</Link>
                        </p>
                    </div>
                </div>
            </section>

            {/* Featured Products Showcase Carousel */}
            <section id="featured-products" className="py-5 bg-light">
                <div className="container">
                    <div className="row align-items-center">
                        <div className="col-md-5">
                            <h2 className="section-title text-start">Polecane Produkty</h2>
                            <p className="lead text-muted text-start">Odkryj nasze bestsellery – produkty najczęściej wybierane przez naszych klientów za ich unikalny design i najwyższą jakość wykonania. Każdy z nich to połączenie innowacji i rzemiosła.</p>
                            <div className="d-flex justify-content-start mt-4">
                                <button className="btn btn-outline-dark me-2" type="button" data-bs-target="#featuredProductCarousel" data-bs-slide="prev">
                                    <i className="bi bi-arrow-left"></i> Poprzedni
                                </button>
                                <button className="btn btn-outline-dark" type="button" data-bs-target="#featuredProductCarousel" data-bs-slide="next">
                                    Następny <i className="bi bi-arrow-right"></i>
                                </button>
                            </div>
                        </div>
                        <div className="col-md-7">
                            <div id="featuredProductCarousel" className="carousel slide carousel-fade" data-bs-ride="carousel">
                                <div className="carousel-inner">
                                    {featuredProducts.map((product, index) => (
                                        <div className={`carousel-item ${index === 0 ? 'active' : ''}`} key={product.id}>
                                            <Link to={`/products/${product.id}`} className="text-decoration-none">
                                                <div className="position-relative" style={{ overflow: 'hidden' }}>
                                                    <img src={`${process.env.PUBLIC_URL}${product.mainImage}`} className="d-block w-100 product-card-img" alt={product.name} />
                                                    <div className="position-absolute top-0 start-0 p-2">
                                                        {product.isNew && <span className="badge bg-success me-1">NOWOŚĆ</span>}
                                                        {hasPromotion(product) && <span className="badge bg-danger me-1">PROMOCJA</span>}
                                                        {product.isBestseller && <span className="badge bg-warning text-dark">BESTSELLER</span>}
                                                    </div>
                                                    <div className="featured-carousel-caption">
                                                        <h5>{product.name}</h5>
                                                        <p className="card-text text-muted fw-bold">od {getLowestPrice(product).toFixed(2)} PLN</p>
                                                    </div>
                                                </div>
                                            </Link>
                                        </div>
                                    ))}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>

            {/* Key Features Section */}
            <section id="key-features" className="py-5">
                <div className="container">
                    <div className="row text-center g-4">
                        <div className="col-lg-3 col-md-6">
                            <div className="feature-card">
                                <i className="bi bi-award fs-2 mb-3"></i>
                                <h3>Polska Produkcja</h3>
                                <p className="text-muted">Wszystkie nasze produkty powstają lokalnie z najwyższą starannością.</p>
                            </div>
                        </div>
                        <div className="col-lg-3 col-md-6">
                            <div className="feature-card">
                                <i className="bi bi-leaf fs-2 mb-3"></i>
                                <h3>Eko Materiały</h3>
                                <p className="text-muted">Drukujemy z użyciem biodegradowalnych i przyjaznych dla środowiska filamentów.</p>
                            </div>
                        </div>
                        <div className="col-lg-3 col-md-6">
                            <div className="feature-card">
                                <i className="bi bi-gem fs-2 mb-3"></i>
                                <h3>Unikalne Piękno</h3>
                                <p className="text-muted">Każdy projekt jest unikatowy, łącząc w sobie estetykę i funkcjonalność.</p>
                            </div>
                        </div>
                        <div className="col-lg-3 col-md-6">
                            <div className="feature-card">
                                <i className="bi bi-check-circle fs-2 mb-3"></i>
                                <h3>Gwarancja Jakości</h3>
                                <p className="text-muted">Zapewniamy wysoką jakość wykonania i precyzję każdego detalu.</p>
                            </div>
                        </div>
                    </div>
                </div>
            </section>

            {/* Shop Online CTA Section */}
            <section className="py-5 bg-light">
                <div className="container text-center">
                    <h2 className="section-title">Kupuj Wygodnie Online</h2>
                    <p className="lead text-muted col-md-8 mx-auto">Nasza pełna kolekcja jest dostępna na wyciągnięcie ręki. Przeglądaj, wybieraj i zamawiaj z łatwością, bez wychodzenia z domu.</p>
                    {configData && configData.links && (
                        <a 
                            href={configData.links.vinted} 
                            target="_blank" 
                            rel="noopener noreferrer" 
                            className="btn btn-primary btn-lg mt-3"
                        >
                            Przejdź do Sklepu
                        </a>
                    )}
                </div>
            </section>

            {/* Explore More Section */}
            <section className="py-5">
                <div className="container">
                    <div className="row g-4">
                        <div className="col-md-6">
                            <Link to="/about-us" className="text-decoration-none text-dark">
                                <div className="explore-card">
                                    <h3>Poznaj Naszą Historię</h3>
                                    <p className="text-muted">Dowiedz się więcej o naszej pasji i filozofii, która napędza każdy projekt.</p>
                                    <span className="fw-bold">O nas <i className="bi bi-arrow-right-short"></i></span>
                                </div>
                            </Link>
                        </div>
                        <div className="col-md-6">
                            <Link to="/contact" className="text-decoration-none text-dark">
                                <div className="explore-card">
                                    <h3>Masz Pytania?</h3>
                                    <p className="text-muted">Jesteśmy tutaj, aby pomóc. Skontaktuj się z nami w sprawie niestandardowych zamówień lub zapytań.</p>
                                    <span className="fw-bold">Kontakt <i className="bi bi-arrow-right-short"></i></span>
                                </div>
                            </Link>
                        </div>
                    </div>
                </div>
            </section>
        </>
    );
};

export default HomePage;
